import React from "react"
import stl from '../../STYLES/MAIN_PAGE/top-section.module.scss'


const Header = () => {
    return (
        <section className = {stl.section}>
          <div className = {stl.container}>
            <h1>FIND YOU JOB</h1>
            <h2>Better and Faster</h2>
            <p style = {{marginBottom:'50px'}}>Find your job with ssaavvaa website for searching jobs</p>
            <img alt = 'jobs' src = {require('../../images/JoinOurTeam.png')} />
            </div>
        </section>
    )
}

export default Header;